import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import Timer from '../../components/Timer';
import CustomLink from '../../components/CustomLink';
import { useHistory } from "react-router-dom";
import './index.scss';

import * as timerActions from '../../redux/timer/timer.actions';
import * as selectors from '../../redux/rootReducer';

const InitialTestLecture = ({
  authUser,
  onResetTimer,
  onStartTimer,
  onStopTimer,
}) => {
  const history = useHistory();

  useEffect(() => {
    onResetTimer();
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    onStartTimer();
  }, [onStartTimer]);

  useEffect(() => {
    if (authUser.has_completed_initial_test) {
      history.push("/dashboard");
    }
  }, [authUser, history]);

  return (
    <Fragment>
      <div className="page-container--initial-test-lecture">
        <div className="initial-test-lecture">
          <h1 className="initial-test-lecture__title">
            Lectura de diagnstico
          </h1>
          <div className="initial-test-lecture__timer-container">
            <div className="initial-test-lecture__timer-label">
              Tiempo de lectura:
            </div>
            <Timer />
          </div>
          <div className="initial-test-lecture__lecture-container">
            <div className="initial-test-lecture__lecture-title">
              El precio del humo
            </div>
            <div className="initial-test-lecture__text">
              <p>
                Un da, un campesino fue a la ciudad a vender sus productos. De regreso a casa entr en una posada a
                descansar un rato. Como era da de mercado, la posada se encontraba llena de gente.
              </p>
              <p>
                - Qu quieres comer? - le pregunt el posadero.
              </p>
              <p>
                - Una hogaza de pan y un jarrillo de vino - respondi el campesino.
              </p>
              <p>
                Mientras el posadero se alejaba, el campesino fij sus ojos en una pieza que estaba asndose en la chimenea y
                que desprenda un olor delicioso Cunto le gustara tomar un poco de aquella carne! Pero... A saber cunto costaba!
              </p>
              <p>
                Al cabo de un rato, el posadero regres con el pan y con el jarrillo de vino. El campesino empez a comer sin
                poder apartar los ojos del asado... ola tan bien!
              </p>
              <p>
                De pronto, tuvo una idea. Se levant con el pan en la mano y se acerc al fuego. Coloc el pan sobre el humo
                que despeda el asado y esper unos minutos. Cuando el pan se impregn bien de aquel olor tan suculento, lo
                retir del fuego y se dispuso a comer. Pero al ir a morderlo oy una voz que gritaba:
              </p>
              <p>
                - Te crees muy listo, verdad? Intentabas engaarme, pero tendrs que pagar lo que me has robado.
              </p>
              <p>
                Los gritos del posadero despertaron la curiosidad de la gente. Las conversaciones se interrumpieron y todo el mundo mir hacia los dos hombres.
              </p>
              <p>
                - Yo...yo no te he quitado nada. Te pagar el pan y el vino.
              </p>
              <p>
                - S, claro... y el humo, qu? Acaso no piensas pagarlo?
              </p>
              <p>
                El campesino, sin salir de su asombro, intentaba defenderse:
              </p>
              <p>
                - El humo no vale nada, pens que no te importara...
              </p>
              <p>
                - Cmo que el humo no vale nada? Todo lo que hay en esta posada es mo. Y quien lo quiera, debe pagar por ello.
              </p>
              <p>
                En ese momento, un noble que se encontraba comiendo en la posada con otros ilustres caballeros intervino en la discusin:
              </p>
              <p>
                - Clmate, posadero! Cunto pides por el humo?
              </p>
              <p>
                - Me conformo con cuatro monedas- respondi satisfecho el posadero. El pobre campesino exclam preocupado:
              </p>
              <p>
                - Cuatro monedas! Es todo lo que he ganado hoy.
              </p>
              <p>
                Entonces el noble se acerc al campesino y le dijo algo en voz baja. El campesino abri su bolsa y le dio sus cuatro monedas al caballero.
              </p>
              <p>
                - Escucha, posadero- dijo el noble haciendo sonar en su mano las monedas- Ya ests pagado.
              </p>
              <p>
                - Cmo que ya estoy pagado? Dadme las monedas!
              </p>
              <p>
                Clin, clin!, sonaban las monedas en la mano del noble.
              </p>
              <p>
                -Las monedas? -pregunt el posadero-.
              </p>
              <p>
                -Acaso se comi la carne el campesino?
              </p>
              <p>
                l slo cogi el humo. Pues para pagar el humo del asado bastar con el ruido de las monedas.Y ante las risas de todos, el posadero no tuvo ms remedio que volver a su trabajo y dejar marchar tranquilamente al campesino.
              </p>
            </div>
            <hr></hr>
            <CustomLink
              className="initial-test-lecture__link"
              to="/initial-test-questions"
              onClick={onStopTimer}
            >
              Realizar prueba
            </CustomLink>
          </div>

        </div>
      </div>
    </Fragment>
  );
}

const mapStateToProps = (state) => ({
  authUser: selectors.getAuthUser(state),
});

const mapDispatchToProps = (dispatch) => ({
  onResetTimer: () => dispatch(timerActions.resetTimer()),
  onStartTimer: () => dispatch(timerActions.startTimer()),
  onStopTimer: () => dispatch(timerActions.stopTimer()),
});

export default connect(mapStateToProps, mapDispatchToProps)(InitialTestLecture);
